import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-issue-receive-goods',
  templateUrl: './issue-receive-goods.component.html',
  styleUrls: ['./issue-receive-goods.component.scss']
})
export class IssueReceiveGoodsComponent implements OnInit {

  searchValue: any;
  salesOrder: Array<any> = []
  salesSubOrder: Array<any> = []
  heightScroll: any;
  public date: Date[] = [new Date(), new Date()];
  load_status: boolean = false;
  dataRestore: any = {};
  bill_selected: any = {}
  id_branch: string = this.vhQuerySales.getDefaultBranch()._id
  /**Gii hn thi gian cho xem bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < this.minDate.getTime() - 24 * 60 * 60 * 1000) return true
    return false
  }
  minDate: Date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 30)
  infoShop: any = {}
  constructor(
    private router: Router,
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private cdRef: ChangeDetectorRef,
    private vhAuth: VhAuth
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    this.dataRestore = this.router.getCurrentNavigation().extras.state?.dataRestore || {}
  }

  ngOnInit(): void {
    this.initState()

  }
  initState() {
    if (this.dataRestore.id_shop_selected) {
      this.date = this.dataRestore.date
      this.searchValue = this.dataRestore.searchValue
      this.salesOrder = this.dataRestore.salesOrder
      this.salesSubOrder = this.salesOrder
      if (this.dataRestore['update']) {
        this.dataRestore['update'] = false;
        this.showReport()
      }
      else
        this.load_status = true;
    }
    this.infoShop = this.vhQuerySales.getlocalShopListLazada()[0];
    this.getMinDate()
  }
  /** 
   * Ly ngy gii hn c xem bo co
   * @example this.getMinDate()
  */
  getMinDate() {
    let date = new Date(Date.now() - 24 * 60 * 60 * 1000 * 100)
    this.vhQuerySales.getValidityDate(date).then((validitydate: Date) => {
      this.minDate = validitydate
      if ((this.minDate.getTime()) < new Date(this.vhAuth.getStore().registrationdate).getTime()) {
        this.disabledDate = (current: Date): boolean => {
          if (current.getTime() < new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000) return true
          return false
        }
      }
    })
  }
  /**Tm kim ha n */
  search(value: string) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.salesOrder = this.vhAlgorithm.searchList(val, this.salesSubOrder, ['order_id'])
    } else this.salesOrder = [...this.salesSubOrder]
  }
  /**
   * v trang qun l pht hnh ha n
   * @example this.goBack()
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice'], {
      state: { dataRestore: this.dataRestore }
    })
  }
  /**
   * bt s kin thay i ngy xem bo co
   * @example this.changeTime('2023-06-09T04:14:30.367Z')
   */
  changeTime(event) {
    if (event[1].getTime() - event[0].getTime() <= 2678400000) {
    } else {
      this.vhComponent.showToast(2000, this.lang.translate("The time to view no more than 30 days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + 2678313600)]
    }
  }
  /**
   * get ha n lazada
   * @example this.showReport()
   */
  showReport() {
    this.load_status = true;
    this.salesOrder = []
    this.salesSubOrder = []
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.vhQuerySales.getSalesOrderDetailListLazada(new Date(this.date[0].setHours(0, 0, 0)).toISOString(), new Date(this.date[1].setHours(23, 59, 59, 59)).toISOString(), this.infoShop.access_token, this.infoShop._id)
        .then((result: Array<any>) => {
          result.forEach(item => {
            if (item.statuses.length > 1) {
              let count = 0
              item.order_items.forEach(product => { if (product.status == 'canceled') count++ })
              item.deliveredProduct = item.order_items.length - count
              item.canceledProduct = count
            }
            if (item.verify_status_received_goods) this.salesOrder.push({ id_shop: this.infoShop._id, ...item, total_order: this.calculateTotal(item) })
          })
          this.salesSubOrder = [...this.salesOrder];
          this.vhComponent.hideLoading(0);
        }).catch(err => this.vhComponent.hideLoading(0))
    })
  }
  /**
   * tnh li tng tin
   * @param bill 
   * @returns number
   * @example let total = this.calculateTotal(bill)
   */
  calculateTotal(bill) {
    return +bill['price'] - bill['voucher_platform'] - bill['voucher_seller'] + bill['shipping_fee']
  }
  changeShop(event) { }
  ngAfterViewChecked() {
    if (document.querySelector("#Lazada-issue-invoice") && document.querySelector(".ant-table-thead") && document.querySelector(".Lazada-issue-invoice-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#Lazada-issue-invoice").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".Lazada-issue-invoice-header").clientHeight - 90 + "px";
    }

    this.cdRef.detectChanges();
  }

  /**
   * 
   * @param bill 
   */
  gotoCreateBill(bill: any) {
    //tm customer  loi tr bug khch hng b xa ri
    let customer: any = this.vhQuerySales.getlocalCustomer(this.infoShop.data?.id_customer)
    if (customer) {
      this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice/issue-receive-goods/add'], {
        state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, billLazada: bill }
      })
    } else this.vhComponent.alertMessageDesktop('warning', this.lang.translate('Please update customer in settings'))
  }
  gotoDetail(bill: any) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice/issue-receive-goods/detail'], {
      state: { dataRestore: { ...this.dataRestore, searchValue: this.searchValue, id_shop_selected: this.infoShop._id, date: this.date, salesOrder: this.salesOrder }, id: bill.id_bill_received_goods, billLazada: bill }
    })
  }

  renderStatus(status) {
    switch (status) {

      case 2: case 3: return this.lang.translate('Wait for confirmation');
      case 6: return this.lang.translate('Delivering');
      case 7: return this.lang.translate('Delivered');
      case 11: case 12: case 14: case 15: case 19: return this.lang.translate('Trouble')
      case 13: return this.lang.translate("Cancelled");
      case 8: return this.lang.translate("Completed")
      case 10: return this.lang.translate("ng")
      case 21: return this.lang.translate("ang i tr")
      case 22: return this.lang.translate("i tr thnh cng")
      case 23: return this.lang.translate("Ch Lazada x l")
    }
  }
  gotoDetailLazada(bill) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/lazada/issue-invoice/issue-receive-goods/detail-order'],
      {
        state: {
          bill: bill,
          dataRestore: {
            salesOrder: this.salesOrder, searchValue: this.searchValue,
            id_shop_selected: this.infoShop._id, listProduct: this.dataRestore.listProduct,
            date: this.date,
            salesSubOrder: this.salesSubOrder,
          }
        }
      })
  }
}
